import './app.scss';

import Inview from './js/inview';
import Swipers from './js/swipers';
import data from './data/basic.json';
import insertVideo from './js/utils/functions/insertVideo';

const addVideos = () => {
    data.heroes.forEach((hero) => {
        const videoBlockId = `video${hero.id}`;

        insertVideo(videoBlockId, hero.video.link, hero.video.ptLink);
    });
};

function start() {
    setTimeout(() => {
        const preloaderEl = document.getElementById('preloader');

        preloaderEl.classList.add('hide');

        setTimeout(() => {
            preloaderEl.remove();
        }, 350);
    }, 2500);

    const inview = new Inview();
    const swipers = new Swipers();

    addVideos();

    if (inview) console.log('animate started');
    if (swipers) console.log('swipers init');
}

start();
