import Swiper, { Pagination } from 'swiper';
// import data from '../data/basic.json';

export default class Swipers {
    constructor() {
        if ((window.innerWidth <= 430 || window.innerHeight <= 430) && window.innerWidth <= window.innerHeight) {
            this.initSwipers();
        }
    }

    // eslint-disable-next-line
    initSwipers() {
        // * example for array of swiper
        Array.from(document.querySelectorAll('.swiper-presentation')).forEach((swiperEl) => {
            // eslint-disable-next-line
            const swiperInstance = new Swiper(swiperEl, {
                modules: [Pagination],
                pagination: {
                    el: '.hero-presentation__pagination'
                }
            });
        });

        Array.from(document.querySelectorAll('.swiper-goods')).forEach((swiperEl) => {
            // eslint-disable-next-line
            const swiperInstance = new Swiper(swiperEl, {
                modules: [Pagination],
                pagination: {
                    el: '.hero-goods__pagination'
                }
            });
        });
    }
}
